<template>
	<div :style="'width: 100%; height: 40rem; display: inline-block;background-image: url('+background_image+');background-size: 100% 100%;'">
		<Row style="margin: 5% 20%;height: 480px;">
			<Col span="12">
			<div style="height: 480px; background: rgba(0,0,0,.4);color: #FFFFFF;text-align: left;">
				<div style="padding: 40px;">
					<h1>
						
					</h1>
					<p style="margin-top: 20px;line-height: 30px;">+
						<span style="border-bottom: 1px solid #f06c03;
    padding: 5px 0;">O20</span></p>
				</div>
			</div>
			</Col>
			<Col span="12">
			<div style="height: 480px;background-color: #fff;border: 1px solid #fff;">
				<div style="padding: 40px;">
					<Tabs value="name1">
						<TabPane label="" name="name1">
							<h2 style="margin-bottom: 30px;"></h2>
							<Form ref="form_login" :model="form_login" :rules="ruleInline" inline>
								<FormItem prop="username" style="width:98%">
									<Input type="text" v-model="form_login.username" placeholder="username">
									<Icon type="ios-person-outline" slot="prepend"></Icon>
									</Input>
								</FormItem>
								<FormItem prop="password" style="width:98%">
									<Input type="password" v-model="form_login.password" placeholder="Password">
									<Icon type="ios-lock-outline" slot="prepend"></Icon>
									</Input>
								</FormItem>
								<FormItem>
									<Button type="primary" @click="handleSubmit('form_login')"></Button>
								</FormItem>
							</Form>
						</TabPane>
						<TabPane label="" name="name2">
							<h2 style="margin-bottom: 30px;"></h2>
							<Form ref="form_register" :model="form_register" :rules="ruleInline_register" inline>
								<FormItem prop="username" style="width:98%">
									<Input type="text" v-model="form_register.username" placeholder="">
									<Icon type="ios-person-outline" slot="prepend"></Icon>
									</Input>
								</FormItem>
								<FormItem prop="password" style="width:98%">
									<Input type="password" v-model="form_register.password" placeholder="">
									<Icon type="ios-lock-outline" slot="prepend"></Icon>
									</Input>
								</FormItem>
								<FormItem prop="email" style="width:98%">
									<Input type="text" v-model="form_register.email" placeholder="">
									<Icon type="ios-mail-outline" slot="prepend"/>
									</Input>
								</FormItem>
								<FormItem prop="code" style="width:98%">

									<Row :gutter="16">
										<Col span="16">
										<Input type="text" v-model="form_register.code" placeholder="">
										</Icon><Icon type="md-code-working" slot="prepend"/>
										</Input>
										</Col>
										<Col span="4">
										<Button @click="sendCode()" :disabled="send">
											<span v-if="!send"></span>
											<span v-else></span>
										</Button>
										</Col>
									</Row>
								</FormItem>
								<FormItem>
									<Button type="primary" @click="handleSubmit_register('form_register')"></Button>
								</FormItem>
							</Form>
						</TabPane>
					</Tabs>

				</div>
			</div>

			</Col>
		</Row>


	</div>
</template>
<script>
	export default {
		data() {
			return {
				send: false,
				background_image: this.$global.background_image,
				form_login: {
					username: '',
					password: ''
				},
				form_register: {
					username: '',
					password: '',
					email: '',
					code: ''
				},
				ruleInline: {
					username: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					password: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							type: 'string',
							min: 6,
							message: '6',
							trigger: 'blur'
						}
					]
				},
				ruleInline_register: {
					username: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					email: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							type: 'email',
							message: '',
							trigger: 'blur'
						}
					],
					code: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					password: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							type: 'string',
							min: 6,
							message: '6',
							trigger: 'blur'
						}
					]
				}
			}
		},
		methods: {
			sendCode() {
				if (this.form_register.email == '') {
					this.$Message.error('!');
					return
				}
				this.send = true
				var email = this.form_register.email
				this.$api.getRequest(this.$url.INTERFACES.sendCode + '?email=' + email).then(res => {
					if (res.status == 200) {
						this.$Message.success('!');
					} else {
						this.$Message.error('!');
					}
				})
			},
			handleSubmit_register(name) {
				this.$refs[name].validate((valid) => {
					if (valid) {
						this.register()
					} else {
						this.$Message.error('Fail!');
					}
				})
			},
			register() {
				var form_register = {
					username: this.form_register.username,
					password: this.form_register.password,
					email: this.form_register.email,
					code: this.form_register.code,
				}
				this.$api.postRequest(this.$url.INTERFACES.register, form_register).then(res => {
					if (res.status == 200) {
						this.$Message.success('!');
						this.form_register = {
							username: '',
							password: '',
							email: '',
							code: ''
						}
					} else {
						this.$Message.success(res.message);
					}
				})
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if (valid) {
						this.login()()
					} else {
						this.$Message.error('Fail!');
					}
				})
			},
			login() {
				var loginForm = {
					username: this.form_login.username,
					password: this.form_login.password
				}
				this.$api.postRequest(this.$url.INTERFACES.login, loginForm).then(res => {
					if (res.status == 1103) {
						this.$Message.success('!');
						localStorage.setItem("id", res.user.id);
						localStorage.setItem("username", res.user.username);
						localStorage.setItem("token", res.token);
						this.$router.push({
							path: '/about'
						});
					} else {
						this.$Message.error(res.message);
					}
				})
			}
		}
	}
</script>
